import React, { useEffect, useState, useRef } from 'react';

export default function TrackPlayer({ cameraState }) {
  const overlayStyle = {
    position: 'fixed',
    left: '50vw',
    top: '10vh',
    zIndex: 2,
    color: '#fff',
    pointerEvents: 'none',
    textAlign: 'center',
    transition: 'left 0.2s, top 0.2s',
    width: '100vw',
  };
  const logoStyle = {
    width: 'min(180px, 40vw)',
    height: 'auto',
    margin: '40px auto 20px auto',
    display: 'block',
    maxWidth: '90vw',
    filter: 'drop-shadow(0 0 24px #6cf) drop-shadow(0 0 8px #fff)',
    pointerEvents: 'none',
    transition: 'filter 0.3s',
  };
  const dividerStyle = {
    width: 370,
    maxWidth: '90%',
    height: 33,
    margin: '40px auto 0 auto',
    display: 'block',
  };
  const footerStyle = {
    textAlign: 'center',
    margin: '20px 0',
    color: '#fff',
    fontFamily: 'inherit',
    fontSize: '1rem',
  };
  const footerLinkStyle = {
    color: '#fff',
    textDecoration: 'none',
    margin: '0 15px',
    transition: 'color 0.3s ease',
  };

  const [tracks, setTracks] = useState([]);
  const [current, setCurrent] = useState(0);
  const audioRef = useRef(null);
  const [isPlaying, setIsPlaying] = useState(false);

  useEffect(() => {
    fetch(process.env.PUBLIC_URL + '/tracks.json')
      .then(res => res.json())
      .then(setTracks)
      .catch(() => setTracks([]));
  }, []);

  useEffect(() => {
    if (audioRef.current && tracks.length > 0) {
      audioRef.current.src = process.env.PUBLIC_URL + '/' + tracks[current].replace('public/', '');
      if (isPlaying) {
        audioRef.current.play();
      } else {
        audioRef.current.pause();
      }
    }
  }, [current, isPlaying, tracks]);

  const play = () => setIsPlaying(true);
  const pause = () => setIsPlaying(false);
  const stop = () => {
    setIsPlaying(false);
    if (audioRef.current) {
      audioRef.current.currentTime = 0;
      audioRef.current.pause();
    }
  };
  const next = () => setCurrent((current + 1) % tracks.length);
  const prev = () => setCurrent((current - 1 + tracks.length) % tracks.length);
  const selectTrack = (idx) => {
    setCurrent(idx);
    setIsPlaying(true);
  };

  return (
    <>
      <div style={overlayStyle}>
        <a href="/" style={{display:'inline-block',marginBottom:10,color:'#6cf',textDecoration:'underline',fontSize:'1.1em'}}>‚Üê Return to Home</a>
        <img src={process.env.PUBLIC_URL + '/logo_asset.png'} alt="logo" style={logoStyle} />
        <h2 style={{margin: 0, color: '#6cf'}}>Track Player</h2>
        <div className="player-container" style={{display:'flex',flexDirection:'column',alignItems:'center',justifyContent:'center',margin:'40px auto 0 auto',width:'100%',maxWidth:500}}>
          <div className="visualizer-box" style={{position:'relative',width:206,height:206,display:'flex',justifyContent:'center',alignItems:'center',background:'#111',borderRadius:16,marginBottom:16}}>
            {/* Visualizer placeholder */}
            <div style={{position:'absolute',zIndex:0,width:'100%',height:'100%',background:'linear-gradient(135deg,#6cf 10%,#a0f 90%)',opacity:0.12,borderRadius:16}}></div>
            <img src={process.env.PUBLIC_URL + '/trackselection-menu-border@2x@2x.png'} alt="Border" style={{position:'absolute',width:'130%',height:'130%',zIndex:2,pointerEvents:'none',left:'-15%',top:'-15%'}} loading="lazy" />
            <audio ref={audioRef} controls style={{position:'absolute',bottom:8,left:'50%',transform:'translateX(-50%)',width:'90%',zIndex:3,background:'#222',borderRadius:8}} />
          </div>
          <div id="track-name" style={{margin:'18px auto 0 auto',textAlign:'center',color:'white',fontSize:'1.2em',fontFamily:'MS Gothic, monospace',maxWidth:400,overflow:'hidden',textOverflow:'ellipsis',whiteSpace:'nowrap'}}>
            {tracks.length > 0 ? tracks[current].replace('public/', '') : 'No track selected'}
          </div>
          <div id="player-controls" style={{margin:'30px auto 0 auto',display:'flex',justifyContent:'center',alignItems:'center',gap:24,width:400,height:80,fontSize:'2em'}}>
            <button onClick={prev} style={{background:'none',border:'none',cursor:'pointer'}}>
              <img src={process.env.PUBLIC_URL + '/trackplayer-lasttrackbutton.png'} alt="Prev" style={{height:'3em',verticalAlign:'middle'}} loading="lazy" />
            </button>
            <button onClick={isPlaying ? pause : play} style={{background:'none',border:'none',cursor:'pointer'}}>
              <img src={process.env.PUBLIC_URL + (isPlaying ? '/trackplayer-pausebutton.png' : '/trackplayer-playbutton.png')} alt={isPlaying ? 'Pause' : 'Play'} style={{height:'3em',verticalAlign:'middle'}} loading="lazy" />
            </button>
            <button onClick={stop} style={{background:'none',border:'none',cursor:'pointer'}}>
              <img src={process.env.PUBLIC_URL + '/trackplayer-.png'} alt="Stop" style={{height:'3em',verticalAlign:'middle'}} loading="lazy" />
            </button>
            <button onClick={next} style={{background:'none',border:'none',cursor:'pointer'}}>
              <img src={process.env.PUBLIC_URL + '/trackplayer-nextbutton.png'} alt="Next" style={{height:'3em',verticalAlign:'middle'}} loading="lazy" />
            </button>
          </div>
        </div>
        <div className="track-library" style={{background:'#111',border:'1px solid #222',borderRadius:0,boxShadow:'0 2px 8px rgba(0,0,0,0.15)',width:340,margin:'40px auto 24px auto',padding:'18px 18px 18px 8px',color:'#fff'}}>
          <h3 style={{color:'#6cf',margin:'0 0 10px 0',fontSize:'1.1em',textAlign:'left'}}>Track Library</h3>
          <ul className="track-list" style={{listStyle:'none',padding:0,margin:0}}>
            {tracks.map((track, idx) => (
              <li key={track} style={{marginBottom:8,color:'#ccc',display:'flex',alignItems:'center',gap:10}}>
                <button onClick={() => selectTrack(idx)} style={{background:'none',border:'none',color:'#6cf',cursor:'pointer',fontSize:'1em',padding:'0 6px'}}>‚ñ∂</button>
                <span style={{overflow:'hidden',textOverflow:'ellipsis',whiteSpace:'nowrap',maxWidth:180}}>{track.replace('public/', '')}</span>
                <a href={process.env.PUBLIC_URL + '/' + track.replace('public/', '')} download style={{color:'#fff',textDecoration:'underline',fontSize:'0.95em'}}>download</a>
              </li>
            ))}
          </ul>
        </div>
      </div>
      <div style={{ width: '100%', textAlign: 'center', margin: '40px 0 0 0' }}>
        <img src={process.env.PUBLIC_URL + '/divider.png'} alt="divider" style={dividerStyle} />
      </div>
      <footer style={footerStyle}>
        <a href="https://instagram.com/sinbound.soc" target="_blank" style={footerLinkStyle}>instagram</a>
        <a href="https://twitter.com/sinbound.soc" target="_blank" style={footerLinkStyle}>twitter</a>
        <a href="https://soundcloud.com/sinbound.soc" target="_blank" style={footerLinkStyle}>soundcloud</a>
      </footer>
    </>
  );
} 